import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import AttachMoney from '@material-ui/icons/AttachMoney';

interface iProps {}

export function AddExpense(props: iProps) {
  const [open, setOpen] = React.useState(false);
  const handleClickOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Button color='secondary' title=' ' onClick={handleClickOpen}>
        <AttachMoney />
      </Button>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle> :</DialogTitle>
        <DialogContent>
          <TextField name='description' margin='dense' label='' type='text' fullWidth />
          <TextField name='cost' margin='dense' label='' type='number' fullWidth />
          <TextField name='spendDate' margin='dense' label='' type='date' fullWidth />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color='secondary'>
            
          </Button>
          <Button onClick={handleClose} color='primary'>
            
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
